<template>
  <div>
    <v-app-bar color="deep-purple accent-4" fixed dark>
      <div class="nav">
        <v-row>
          <v-col sm="3">
            <div class="logo">
              <img src="../assets/logo.jpg" alt />
            </div>
          </v-col>
          <v-col sm="8">
            <v-bottom-navigation
              v-model="bottomNav"
              style="display: flex;justify-content: space-between;"
            >
              <v-btn value="0" @click="scrollTo(0)">
                <span>{{ $t("home") }}</span>
              </v-btn>

              <v-btn value="1" @click="scrollTo(1)">
                <span>{{ $t("productDescription") }}</span>
              </v-btn>
              <v-btn value="2" @click="scrollTo(2)">
                <span>{{ $t("productDetails") }}</span>
              </v-btn>
              <v-btn value="3" @click="scrollTo(3)">
                <span>{{ $t("projectProgress") }}</span>
              </v-btn>
              <v-btn value="4" @click="scrollTo(4)">
                <span>{{ $t("people") }}</span>
              </v-btn>
              <v-btn value="5" @click="scrollTo(5)">
                <span>{{ $t("partner") }}</span>
              </v-btn>
            </v-bottom-navigation>
          </v-col>
        </v-row>
      </div>
    </v-app-bar>
    <v-container :fluid="true" class="banner">
      <div>
        <h1 v-if="this.$i18n.locale=='zh'" class="text" style="text-align:center">
          <strong>{{ $t("banner")}}</strong>
        </h1>
        <h1 v-else class="text" style="text-align:center;width:35ch">
          <strong style="font-size:28px;letter-spacing: 0px;">{{ $t("banner")}}</strong>
        </h1>
      </div>
      <canvas id="canvas" class="nav-item"></canvas>
    </v-container>
    <div class="slider nav-item">
      <v-carousel
        :show-arrows="false"
        cycle
        height="100%"
        delimiter-icon="mdi-minus"
        :interval="5000"
        v-model="carouselNum"
      >
        <v-carousel-item v-for="(slider, index) in sliders" :key="index">
          <v-sheet height="100%">
            <v-row align="center" justify="center">
              <div class="item" @click="dialog = true">
                <div
                  class="details"
                  :class="{
                    'details-right': index === 2,
                    'item-gradient': index == carouselNum
                  }"
                >
                  <h3>{{ $t(slider.title) }}</h3>
                  <div class="des" v-html="$t(slider.content)"></div>
                </div>
                <img :src="slider.image" alt width="100%" height="600" />
              </div>
            </v-row>
          </v-sheet>
        </v-carousel-item>
      </v-carousel>
    </div>
    <div class="product nav-item">
      <div class="watch">
        <img class="watchimg" src="../assets/watch.jpg" alt width="100%" />
        <div class="dalong" @click="dialog = true">
          <p>点击商品，填写信息</p>
        </div>
        <div
          class="func"
          v-for="(item, index) in watchFunction"
          :key="index"
          :style="`left:${item.x};top:${item.y}`"
        >
          <div :class="`item-${index}`"></div>
          <div class="func-text">
            {{ $t(item.name) }}
            <div class="func-des">
              <ul v-if="index === 0" style="display:flex">
                <li style="margin:0 10px;" v-for="d in funcDetailsArr[index]" :key="d">{{ $t(d) }}</li>
              </ul>
              <ul v-else>
                <li v-for="d in funcDetailsArr[index]" :key="d">{{ $t(d) }}</li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="project-progress nav-item">
      <h4>{{$t('advance')}}</h4>
      <v-timeline>
        <v-timeline-item v-for="num in 11" :key="num" :class="{ 'text-right': num % 2 === 0 }">
          <span class="line_time">
            {{
            $t(`timeLine${num}`)
            }}
          </span>
        </v-timeline-item>
      </v-timeline>
    </div>
    <div class="people nav-item">
      <h4>{{ $t("people") }}</h4>
      <div class="index_content_c">
        <div class="index_content_c_brand">
          <div class="index_content_c_brand_list" v-for="(item,index) in peopleList" :key="index">
            <div class="scaleImg">
              <img class="scaleImg_after" :src="item.imgUrl" />
            </div>
            <div class="index_content_c_brand_text">
              <h1>{{ $t(item.name)}}</h1>
              <div class="index_nav_ul_divider"></div>
              <p>{{$t(item.info)}}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="partner nav-item">
      <h4>{{ $t("partner") }}</h4>
      <div class="partner-img" :class="{ show: bottomNav === '4' }">
        <div>
          <img v-for="(num, index) in partners1" :key="index" :src="num" alt />
        </div>
      </div>
    </div>
    <Footer />
    <Dialog :visable.sync="dialog" @changeDialog="dialog = !dialog" />
  </div>
</template>

<script>
import { draw } from "../tools/circle";
import bg1 from "../assets/bg5.jpg";
import bg2 from "../assets/bg4.jpg";
import bg3 from "../assets/bg3.jpg";
import bg4 from "../assets/bg2.jpg";
import people1 from "../assets/people1.jpg";
import people2 from "../assets/people2.jpg";
import people3 from "../assets/people3.png";
import people4 from "../assets/people4.png";
import people5 from "../assets/people5.png";
import people6 from "../assets/people6.png";
import people7 from "../assets/people7.png";
import people8 from "../assets/people8.png";
import people9 from "../assets/people9.png";
import Footer from "./Foot.vue";
import Dialog from "../components/dialog";
export default {
  name: "Home",
  data() {
    return {
      carouselNum: 0,
      partners1: [
        require("../assets/partners/partner1.png"),
        require("../assets/partners/partner2.png"),
        require("../assets/partners/partner3.png"),
        require("../assets/partners/partner4.png"),
        require("../assets/partners/partner6.png"),
        require("../assets/partners/partner9.png")
      ],
      bottomNav: "0",
      dialog: false,
      icons: ["mdi-home", "mdi-email", "mdi-calendar", "mdi-delete"],
      sliders: [
        {
          image: bg1,
          title: "bg1-title",
          content: "bg1"
        },
        {
          image: bg2,
          title: "bg2-title",
          content: "bg2"
        },
        {
          image: bg3,
          title: "bg3-title",
          content: "bg3"
        },
        {
          image: bg4,
          title: "bg4-title",
          content: "bg4"
        }
      ],
      peopleList: [
        {
          imgUrl: people1,
          name: "people1-name",
          info: "people1-info"
        },
        {
          imgUrl: people2,
          name: "people2-name",
          info: "people2-info"
        },
        {
          imgUrl: people3,
          name: "people3-name",
          info: "people3-info"
        },
        {
          imgUrl: people4,
          name: "people4-name",
          info: "people4-info"
        },
        {
          imgUrl: people5,
          name: "people5-name",
          info: "people5-info"
        },
        {
          imgUrl: people6,
          name: "people6-name",
          info: "people6-info"
        },
        {
          imgUrl: people7,
          name: "people7-name",
          info: "people7-info"
        },
        {
          imgUrl: people8,
          name: "people8-name",
          info: "people8-info"
        },
        {
          imgUrl: people9,
          name: "people9-name",
          info: "people9-info"
        }
      ],
      watchFunction: [
        {
          name: "voiceAlls",
          x: "55%",
          y: "10%"
        },
        {
          name: "info",
          x: "28%",
          y: "30%"
        },
        {
          name: "collection",
          x: "22%",
          y: "53%"
        },
        {
          name: "taxi",
          x: "28%",
          y: "76%"
        },
        {
          name: "car",
          x: "70%",
          y: "76%"
        },
        {
          name: "fitness",
          x: "75%",
          y: "53%"
        },
        {
          name: "photos",
          x: "71%",
          y: "30%"
        }
      ],
      funcDetailsArr: [
        ["voiceAlls1", "voiceAlls2", "voiceAlls3"],
        ["info1", "info2"],
        ["collection1", "collection2"],
        ["taxi1", "taxi2", "taxi3"],
        ["car1", "car2", "car3"],
        ["fitness1", "fitness2", "fitness3"],
        ["photos1", "photos2"]
      ]
    };
  },
  components: {
    Footer,
    Dialog
  },
  methods: {
    // 滚动监听器
    onScroll() {
      // 获取所有锚点元素
      const navContents = document.querySelectorAll(".nav-item");
      // 所有锚点元素的 offsetTop
      const offsetTopArr = [];
      navContents.forEach(item => {
        offsetTopArr.push(item.offsetTop);
      });
      // 获取当前文档流的 scrollTop
      const scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      // 定义当前点亮的导航下标
      let navIndex = 0;
      for (let n = 0; n < offsetTopArr.length; n++) {
        // 如果 scrollTop 大于等于第n个元素的 offsetTop 则说明 n-1 的内容已经完全不可见
        // 那么此时导航索引就应该是n了
        if (scrollTop + 350 >= offsetTopArr[n]) {
          navIndex = n;
        }
      }
      navContents[navIndex].classList.add("nav-item-show");
      this.bottomNav = String(navIndex);
    },
    // 跳转到指定索引的元素
    scrollTo(index) {
      this.bottomNav = String(index);
      // 获取目标的 offsetTop
      // css选择器是从 1 开始计数，我们是从 0 开始，所以要 +1
      const targetOffsetTop = document.querySelectorAll(".nav-item")[index]
        .offsetTop;

      // 获取当前 offsetTop
      let scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      // 定义一次跳 50 个像素，数字越大跳得越快，但是会有掉帧得感觉，步子迈大了会扯到蛋
      const STEP = 50;
      // 判断是往下滑还是往上滑
      if (scrollTop > targetOffsetTop) {
        // 往上滑
        smoothUp();
      } else {
        // 往下滑
        smoothDown();
      }
      // 定义往下滑函数
      function smoothDown() {
        // 如果当前 scrollTop 小于 targetOffsetTop 说明视口还没滑到指定位置
        if (scrollTop < targetOffsetTop) {
          // 如果和目标相差距离大于等于 STEP 就跳 STEP
          // 否则直接跳到目标点，目标是为了防止跳过了。
          if (targetOffsetTop - scrollTop >= STEP) {
            scrollTop += STEP;
          } else {
            scrollTop = targetOffsetTop;
          }
          document.body.scrollTop = scrollTop;
          document.documentElement.scrollTop = scrollTop;
          // 关于 requestAnimationFrame 可以自己查一下，在这种场景下，相比 setInterval 性价比更高
          requestAnimationFrame(smoothDown);
        }
      }
      // 定义往上滑函数
      function smoothUp() {
        if (scrollTop > targetOffsetTop) {
          if (scrollTop - targetOffsetTop >= STEP) {
            scrollTop -= STEP;
          } else {
            scrollTop = targetOffsetTop;
          }
          document.body.scrollTop = scrollTop;
          document.documentElement.scrollTop = scrollTop;
          requestAnimationFrame(smoothUp);
        }
      }
    }
  },
  mounted() {
    draw();
    window.addEventListener("scroll", this.onScroll, false);
  },
  destroy() {
    // 必须移除监听器，不然当该vue组件被销毁了，监听器还在就会出错
    window.removeEventListener("scroll", this.onScroll);
  }
};
</script>
