<template>
  <div id="login">
    <div class="register_box">
      <h2>bitwatch 后台管理系统</h2>
      <div class="register_phone">
        <p>用户身份验证</p>
        <v-form ref="form" v-model="valid">
          <div class="register_box_item">
            <img src="@/assets/2.phoneicon.png" />
            <v-text-field v-model="usrName" :rules="nameRules" :counter="10" label="用户名" required></v-text-field>
          </div>
          <div class="register_box_item">
            <img src="@/assets/3.pwdicon.png" />
            <v-text-field v-model="pwd" type="password" :rules="passwordRules" label="密码" required></v-text-field>
          </div>
        </v-form>
        <v-btn class="btn" @click="login">登录</v-btn>
      </div>
      <v-alert v-if="alert" dense text :type="type">{{tips}}</v-alert>
    </div>
  </div>
</template>

<script>
export default {
  name: "index",
  data() {
    return {
      usrName: "",
      alert: false,
      nameRules: [
        v => !!v || "请输入账号",
        v => (v && v.length <= 11) || "账号不能超过11位"
      ],
      passwordRules: [v => !!v || "请输入密码"],
      pwd: "",
      valid: true,
      type: "success",
      tips: ""
    };
  },
  methods: {
    login() {
      let params = { usrName: this.usrName, pwd: this.pwd };

      this.$axios.post("/api/usr/login", this.formData(params)).then(res => {
        console.log(res);
        if (res.data.code == "OK") {
          this.type = "success";
          this.alert = true;
          this.tips = "登录成功";
          setTimeout(() => {
            this.$router.push({
              name: "bgManage"
            });
          }, 500);
        } else {
          this.type = "error";
          this.alert = true;
          this.tips = res.data.descp;
        }
      });
    },
    formData(data) {
      let ret = "";
      for (let it in data) {
        ret +=
          encodeURIComponent(it) + "=" + encodeURIComponent(data[it]) + "&";
      }
      return ret;
    },
    validate() {
      if (this.$refs.form.validate()) {
        this.snackbar = true;
      }
    },
    reset() {
      this.$refs.form.reset();
    },
    resetValidation() {
      this.$refs.form.resetValidation();
    }
  }
};
</script>

<style lang="less">
#login {
  .register_box {
    color: #fdfefd;
    height: 100vh;
    background: url("../../assets/banner.jpg");
    background-size: cover;
    text-align: center;
    padding: 100px 0;
  }
  h2 {
    margin-bottom: 40px;
    font-weight: 400;
    margin-right: 23%;
    text-align: right;
  }
  .v-alert {
    width: 200px;
    margin: 0 auto;
    position: fixed;
    top: 48%;
    left: 40%;
  }
  .register_phone {
    position: relative;
    right: -60%;
    width: 280px;
    padding: 30px;
    background: rgba(249, 249, 250, 0.2);
    box-shadow: 0 2px 12px 2px rgba(34, 34, 36, 0.8);
    border-radius: 2px;
    border: 2px solid #2268b0;
    line-height: 70px;
    color: #fdfefd;
    img {
      width: 13px;
      height: 15px;
      margin-right: 10px;
      margin-top: 25px;
    }
  }
  .register_box_item {
    display: flex;
  }
  .v-label--active {
    color: #fff !important;
    caret-color: #fff !important;
  }
  .v-application .primary--text {
    color: #fff !important;
    caret-color: #fff !important;
  }
  .v-label .v-label--active .theme--light {
    color: #fff !important;
    caret-color: #fff !important;
  }
  input {
    color: #fff !important;
  }
  .btn {
    width: 100%;
    margin-top: 50px;
    background: cornflowerblue !important;
    color: white !important;
  }
}
</style>
