<template>
  <div class="dialog" v-show="visable">
    <div>
      <v-card width="500px">
        <v-card-title>
          <span class="headline">提交工单</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="10">
                  <v-text-field
                    label="姓名"
                    persistent-hint
                    v-model="formData.usrName"
                    :rules="[v => !!v || '姓名为必填']"
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="10">
                  <v-text-field
                    label="手机号码"
                    v-model="formData.phoneNum"
                    :rules="[
                      v => !!v || '手机号码为必填',
                      v => /^1[3456789]\d{9}$/.test(v) || '手机号码不正确'
                    ]"
                  ></v-text-field>
                </v-col>
                <v-col cols="10">
                  <v-text-field
                    label="电子邮箱"
                    v-model="formData.email"
                    :rules="[
                      v => !!v || '邮箱为必填',
                      v => /.+@.+\..+/.test(v) || '邮箱格式不正确'
                    ]"
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="10">
                  <v-text-field
                    label="地址"
                    v-model="formData.addr"
                    :rules="[v => !!v || '地址为必填']"
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="10">
                  <v-text-field label="备注" required v-model="formData.remark"></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text @click="close">关闭</v-btn>
          <v-btn text @click="submit">确认</v-btn>
        </v-card-actions>
      </v-card>
    </div>
    <v-snackbar v-model="snackbar" color="success" top :timeout="3000">{{ text }}</v-snackbar>
  </div>
</template>
<script>
export default {
  props: {
    visable: {
      required: true
    }
  },
  data() {
    return {
      valid: false,
      formData: {
        usrName: "",
        phoneNum: "",
        email: "",
        addr: "",
        remark: ""
      },
      snackbar: false,
      text: "提交成功"
    };
  },
  methods: {
    submit() {
      if (this.$refs.form.validate()) {
        //调接口
        this.$axios.post("/api/applyInfo/add", this.formData).then(res => {
          this.close();
        });
      } else {
        this.snackbar = true;
      }
    },
    close() {
      this.$emit("changeDialog");
      this.formData = {
        usrName: "",
        phoneNum: "",
        email: "",
        addr: "",
        remark: ""
      };
    }
  }
};
</script>
<style lang="less">
.dialog {
  width: 100%;
  top: 0;
  position: fixed;
  z-index: 999;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  .v-card {
    margin: 50px auto;
  }
  .v-messages__message {
    color: red;
  }
}
</style>
