import Vue from "vue";
import Router from "vue-router";
import background from "./modules/background";
import home from "../views/Home";
Vue.use(Router);

const router = new Router({
  base: '/',
  // mode: "history",
  routes: [{
      path: "/",
      name: "home",
      component: home,
      meta: {
        title: "BitWatch"
      }
    },
    ...background
  ]
});

router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = to.meta.title;
  }
  next();
});

export default router;