import login from "../../views/login/index";
import bgManage from "../../views/bgManage/index";
import noFound from "../../components/404.vue"
export default [{
    path: "/login",
    name: "login",
    component: login,
    meta: {
      title: "登录"
    }
  },
  {
    path: "/bgManage",
    name: "bgManage",
    component: bgManage,
    meta: {
      title: "后台管理系统"
    }
  },
  {
    path: "*",
    name: "404",
    component: noFound,
    meta: {
      title: "404"
    }
  }
];