<template>
  <div>
    <v-bottom-navigation color="deep-purple" dark>
      <strong>bitWatch</strong>后台管理系统
    </v-bottom-navigation>
    <div class="table">
      <v-list flat>
        <v-subheader>
          <strong>bitWatch</strong>后台管理系统
        </v-subheader>
        <v-list-item-group v-model="item" color="primary">
          <v-list-item v-for="(item, i) in items" :key="i">
            <v-list-item-icon>
              <v-icon v-text="item.icon"></v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title v-text="item.text"></v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>
      </v-list>
      <v-container>
        <v-card-title>
          客户信息
          <v-spacer></v-spacer>
          <v-text-field
            v-model="search"
            append-icon="search"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
        </v-card-title>
        <v-data-table
          :loading="loading"
          loading-text="Loading... Please wait"
          :headers="headers"
          :items="desserts"
          :items-per-page="currentNum"
          :page="currentPage"
          @pagination="changePage"
          :server-items-length="totalSize"
          :footer-props="footerProps"
        ></v-data-table>
      </v-container>
    </div>
  </div>
</template>

<script>
export default {
  name: "bgManage",

  data() {
    return {
      search: "",
      loading: true,
      currentPage: 1,
      currentNum: 15,
      totalSize: 0,
      footerProps: {
        "items-per-page-options": [5, 10, 15]
      },
      item: 1,
      items: [
        { text: "客户申请", icon: "mdi-account" }
        // { text: '用户信息', icon: 'mdi-account' },
      ],
      headers: [
        {
          text: "姓名",
          sortable: false,
          align: "center",
          value: "usrName"
        },
        { text: "手机号", align: "center", sortable: false, value: "phoneNum" },
        { text: "电子邮箱", align: "center", sortable: false, value: "email" },
        { text: "地址", align: "center", sortable: false, value: "addr" },
        { text: "备注", align: "center", sortable: false, value: "remark" }
      ],
      desserts: []
    };
  },
  created() {
    this.getCurrentList();
  },
  methods: {
    getCurrentList() {
      this.$axios
        .post(`/api/applyInfo/list?pageNum=${this.currentPage}`)
        .then(res => {
          if (res.data.code !== "LOGIN") {
            this.loading = false;
            this.desserts = res.data.data.data;
            this.totalSize = res.data.data.total;
          } else {
            this.$router.push({
              name: "login"
            });
          }
        });
    },
    changePage(data) {
      this.currentPage = data.page;
      this.getCurrentList();
    }
  }
};
</script>
<style lang="less">
.table {
  display: flex;
  background: aliceblue;
  height: 100vh;
}
.v-subheader {
  color: #1976d2 !important;
  font-size: 22px !important;
  font-weight: 500 !important;
}
.v-bottom-navigation {
  line-height: 56px !important;
  position: relative !important;
  top: 0 !important;
}
</style>
